.full {
  width:100%;
}

/* links */
a {
  color:#BA209B;
  text-decoration: none;
}

a:hover {
  color: #D424B1;
}

/* paragraphs */
p {
  line-height: 1.5;
}

/*blockquotes*/
blockquote {
  background: #F7F7F7;
  margin:0;
  padding:20px;
}
  blockquote p {
    margin-top: 0;
  }

/*tables*/
table td {
  padding:10px;
}
table tr td:first {
  padding-right: 0;
}

/*def lists*/
dt {
  font-weight: 600;
  padding-bottom: 5px;
}
dd {
  padding:10px;
  margin-left: 10px;
}

/*lists*/
ul li,ol li {
  padding:3px;
}

/*PRE*/

pre {
  background: #F7F7F7;
  padding:10px;
  border-radius: 4px;
  border:1px solid #ccc;
}
